html {
  font-size: 62.5%; }

body {
  margin: 0;
  padding: 0;
  font-size: 1.6rem;
  background-color: #eef3f9;
  font-family: "Montserrat", sans-serif; }

button {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer; }

.main-layout {
  display: grid;
  grid-template-columns: minmax(min-content, max-content) 1fr; }

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0; }

.add-btn {
  border-radius: 8px;
  background-color: #00ca80;
  font-size: 1.2rem;
  padding: 1.06667rem 2.4rem;
  color: white; }

input {
  border: none;
  outline: none; }

.sidebar {
  display: grid;
  background: linear-gradient(0deg, #5089b8 0%, #3a39ff 100%);
  width: 6.4rem;
  height: 100vh;
  grid-template-rows: repeat(4, minmax(min-content, max-content)) 1fr; }
  @media (min-width: 1000px) {
    .sidebar {
      width: 20rem; } }
  .sidebar .logo-container {
    width: inherit;
    display: flex;
    justify-content: center;
    padding-top: 4.8rem;
    padding-bottom: 9.6rem; }
    .sidebar .logo-container .logo {
      font-size: 3.2rem;
      color: #fff; }
      @media (min-width: 1000px) {
        .sidebar .logo-container .logo {
          font-size: 6.4rem; } }
  .sidebar .sidebar-item-container {
    width: inherit;
    display: flex;
    justify-content: center; }
    .sidebar .sidebar-item-container .sidebar-item {
      display: flex;
      align-items: center;
      padding: 1.6rem 0;
      color: #cccccc;
      font-size: 1.4rem;
      font-weight: 100;
      z-index: 1; }
      .sidebar .sidebar-item-container .sidebar-item:hover {
        color: #fff;
        font-weight: 400; }
      .sidebar .sidebar-item-container .sidebar-item .btn-text {
        display: none; }
        @media (min-width: 1000px) {
          .sidebar .sidebar-item-container .sidebar-item .btn-text {
            display: inline; } }
    @media (min-width: 1000px) {
      .sidebar .sidebar-item-container .sidebar-item-icon {
        margin-right: 1.6rem; }
        .sidebar .sidebar-item-container .sidebar-item-icon.fa-edit {
          margin-right: 1.075rem; } }
  .sidebar .sidebar-item-container.selected {
    background-color: #eef3f9;
    border-right: 12px solid #eef3f9;
    margin: -12px 0; }
    .sidebar .sidebar-item-container.selected .sidebar-item {
      color: #24292e;
      font-weight: 700; }
    .sidebar .sidebar-item-container.selected .sidebar-item-icon {
      color: blue; }

.right-bottom-border {
  position: relative;
  border-bottom: 12px solid #eef3f9;
  border-right: 12px solid #eef3f9;
  border-bottom-right-radius: 3rem; }
  .right-bottom-border::after {
    content: "";
    position: absolute;
    bottom: -12px;
    right: -12px;
    border: 15px solid #eef3f9;
    border-color: transparent #eef3f9 #eef3f9 transparent; }

.right-top-border {
  position: relative;
  border-top: 12px solid #eef3f9;
  border-right: 12px solid #eef3f9;
  border-top-right-radius: 3rem; }
  .right-top-border::after {
    content: "";
    position: absolute;
    top: -12px;
    right: -12px;
    border: 15px solid #eef3f9;
    border-color: #eef3f9 #eef3f9 transparent transparent; }

.right-border {
  border-right: 12px solid #eef3f9; }

.search-container {
  border-radius: 8px;
  background-color: #fff;
  padding: 3.2rem;
  margin: 3.2rem 3.2rem 3.2rem 20px; }
  .search-container .search-header {
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-bottom: 3.2rem; }

.search-inputs {
  display: flex;
  width: 100%;
  justify-content: space-between; }

.search-input-container {
  background-color: #f7f8fc;
  padding: 0.8rem 1.06667rem;
  border-radius: 8px;
  min-height: 3.5rem;
  max-height: 3.5rem;
  display: flex;
  align-items: center; }
  .search-input-container i {
    margin-right: 1.6rem;
    color: #c0c4e0; }
  .search-input-container .search-input {
    background-color: transparent;
    width: 30rem;
    font-size: 1.3rem;
    color: #70789e;
    font-weight: 700;
    font-size: 1.3rem; }
    .search-input-container .search-input::placeholder {
      color: #70789e;
      font-weight: 700;
      font-size: 1.3rem; }

.search-filter-container {
  min-height: 3.5rem;
  max-height: 3.5rem;
  border-radius: 8px;
  position: relative;
  background-color: #f7f8fc;
  overflow: hidden;
  transition: 0.3s all ease-in-out; }
  .search-filter-container::after {
    content: "";
    position: absolute;
    border: solid #c0c4e0;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    top: calc(1.75rem - 0.65rem);
    right: 2rem; }
  .search-filter-container.filter {
    max-width: 17rem;
    min-width: 17rem; }
  .search-filter-container input {
    width: 1px;
    height: 1px;
    position: absolute;
    opacity: 0.01; }
  .search-filter-container label {
    position: relative;
    display: flex;
    min-height: 3.5rem;
    max-height: 3.5rem;
    padding-left: 2rem;
    align-items: center;
    cursor: pointer;
    color: #70789e;
    font-weight: 700;
    font-size: 1.3rem; }

.search-filter-container.expanded {
  min-height: 14rem;
  max-height: 14rem; }
  .search-filter-container.expanded label {
    border-bottom: 2px solid #c0c4e0;
    border-right: 2px solid #c0c4e0;
    border-left: 2px solid #c0c4e0;
    padding-left: calc(2rem - 2px); }
    .search-filter-container.expanded label.selected::after {
      content: "";
      position: absolute;
      height: 1rem;
      width: 1rem;
      background-color: #00ca80;
      border-radius: 50%;
      display: inline-block;
      margin: auto;
      top: 0;
      bottom: 0;
      right: 2rem; }
    .search-filter-container.expanded label:last-of-type {
      border-bottom-left-radius: 8px;
      border-bottom-right-radius: 8px; }
    .search-filter-container.expanded label:first-of-type {
      border-top-left-radius: 8px;
      border-top-right-radius: 8px;
      border-top: 2px solid #c0c4e0; }
